.Header_header__burger__38XD7{
    display: block;
    position: relative;
    width: 30px;
    height: 20px;
    z-index: 3;
}
.Header_header__burger__38XD7:before, 
.Header_header__burger__38XD7:after{		/* Стилизуем наши линии с помошью before и after */
    content: '';
    background-color: #ffffff;
    position: absolute;
    width: 100%;
    height: 2px;
    left: 0;
    transition: all 0.3s ease 0s;     /* Анимация при нажатии на бургер */
}
.Header_header__burger__38XD7:before{
    top: 0;               /* Верхняя полоса сверху */
}
.Header_header__burger__38XD7:after{
    bottom: 0;            /* Другая полоса снизу */
}
.Header_header__burger__38XD7 span{
    position: absolute;
    background-color: #ffffff;
    left: 0;
    width: 100%;
    height: 2px;
    top: 9px;
    transition: all 0.3s ease 0s;     /* Анимация при нажатии на бургер */
}
.Header_header__burger__38XD7.Header_active__1dmn0:before {			
    transform: rotate(45deg);			/* Градус верхней палочки крестика */
    top: 9px;
}  
.Header_header__burger__38XD7.Header_active__1dmn0:after {			
    transform: rotate(-45deg);          /* Градус нижней палочки крестика */
    bottom: 9px; 
}
.Header_header__burger__38XD7.Header_active__1dmn0 span{
    transform: scale(0); 				/* Убираем палочку посередине из бургера при нажатии */
}
